/*================== Sass Modules ========================*/
html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  /*================ Colors ==================*/
  --clr-blue-100: hsl(230, 60%, 90%);
  --clr-blue-200: hsl(230, 70%, 80%);
  --clr-blue-300: hsl(230, 70%, 70%);
  --clr-blue-500: hsl(230, 70%, 50%);
  --clr-blue-600: hsl(230, 60%, 40%);
  --clr-white: hsl(0, 100%, 100%);
  --body-bg-color-1: rgb(236, 236, 236);
  --body-bg-color-2: #fff9e8;
  --home-main-bg-color: hsl(0, 100%, 100%);
  --section-one-bg-color: #eaf0fd;
  --section-two-bg-color: #f5f5ff;
  --text-clr: #637181;
  --heading-clr: #3f485a;
  --form-bg: #eaf0fd;
  --form-label-clr: #272e3b;
  --form-link-clr: #383c5e;
  --form-input-bg-clr: hsl(0, 100%, 100%);
  /*!=========== FONT STACKS =============*/
  --font-lato: "Lato", sans-serif;
  --font-montserrat: "Montserrat", sans-serif;
  /*!=========== FONT WEIGHTS ============*/
  --font-wt-400: 400;
  --font-wt-500: 500;
  --font-wt-700: 700;
  /*!============ LINE HEIGHTS ============*/
  --body-lh: 1.6;
  --heading-lh: 1.1;
  /*!================ RADII ================*/
  --rad-4: 4px;
  --rad-6: 6px;
  --rad-round: 50%;
}

.dark--mode {
  --body-bg-color-1: #252b42;
  --body-bg-color-2: #262642;
  --home-main-bg-color: #070f20;
  --section-one-bg-color: #12203f;
  --section-two-bg-color: #091a38;
  --heading-clr: #e7efff;
  --text-clr: hsl(236, 12%, 74%);
  --form-bg: #0f1531;
  --form-label-clr: #f1f6ff;
  --form-link-clr: #b4b9ff;
  --form-input-bg-clr: #12203f;
}

body {
  font: var(--font-wt-400) 1rem/var(--body-lh) var(--font-montserrat);
  background: repeating-linear-gradient(50deg, var(--body-bg-color-1) 3rem, var(--body-bg-color-2) 4.5rem);
  position: relative;
}

@supports (block-size: 13px) and (inline-size: 13px) {
  ::-webkit-scrollbar {
    inline-size: 13px;
    block-size: 13px;
  }
}
@supports not ((block-size: 13px) and (inline-size: 13px)) {
  ::-webkit-scrollbar {
    width: 13px;
    height: 13px;
  }
}
::-webkit-scrollbar-track {
  background-color: var(--clr-white);
  box-shadow: 2px 0 3px 2px rgba(0, 0, 0, 0.1) inset;
}

::-webkit-scrollbar-thumb {
  background-color: var(--clr-blue-300);
  border-radius: var(--rad-4);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: var(--heading-lh);
}

a {
  text-decoration: none;
}

/*============= KEYFRAMES FOR SLIDING ANIMATION =============*/
@keyframes left-slide-animation {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes right-slide-animation {
  0% {
    transform: translateX(150px);
    opacity: 0;
  }
  100% {
    transform: translate(0);
    opacity: 1;
  }
}
/*!================================ HEADER STYLING =====================================*/
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  column-gap: 1rem;
  padding: 0.5rem 1.5rem;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.05), 0 1px 2px rgba(0, 0, 0, 0.1);
}

.logo {
  border-radius: 5px;
  display: block;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.4);
}
@supports (block-size: 0) {
  .logo--md {
    block-size: 3.5rem;
  }
}
@supports not (block-size: 0) {
  .logo--md {
    height: 3.5rem;
  }
}

/*!=========== MAIN NAVIGATION ==========*/
.navigation--main {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
}
.navigation--show {
  display: block;
  border-bottom-left-radius: var(--rad-4);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
}
.navigation__menu {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  gap: 1rem 0.85rem;
  position: relative;
  list-style-type: none;
  padding: 4rem 3rem 2rem;
}
.navigation__menu .color__mode--toggler {
  position: relative;
  background-color: var(--clr-white);
  width: 3.5rem;
  height: 1.7rem;
  border-radius: 40px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  transition: background-color 250ms ease;
}
.navigation__menu .color__mode--toggler.color__mode--toggler--dark {
  background-color: var(--clr-blue-600);
}
.navigation__menu .color__mode--toggler span {
  position: absolute;
  height: 1.4rem;
  top: 0.15rem;
  left: 0.15rem;
  width: 1.4rem;
  border-radius: var(--rad-round);
  background-color: var(--clr-blue-600);
  transition: transform 250ms ease, background-color 250ms ease;
}
:is(.navigation__menu .color__mode--toggler:hover, .navigation__menu .color__mode--toggler:focus) span {
  cursor: pointer;
}
.navigation__menu span.toggler__ball--dark {
  transform: translateX(1.8rem);
  background-color: var(--clr-white);
}
.navigation__hide--btn--wrapper {
  position: absolute;
  top: 10px;
  right: 10px;
}
.navigation__show--btn, .navigation__hide--btn {
  display: inline-block;
  background-color: transparent;
  border: none;
}
.navigation__show--btn ion-icon, .navigation__hide--btn ion-icon {
  border: 2px solid var(--clr-blue-600);
  font-size: 2.1rem;
  color: var(--clr-blue-600);
  padding: 1px 2px;
  border-radius: var(--rad-4);
}
.navigation__show--btn:hover, .navigation__show--btn:focus, .navigation__hide--btn:hover, .navigation__hide--btn:focus {
  cursor: pointer;
}
:is(.navigation__show--btn:hover, .navigation__show--btn:focus, .navigation__hide--btn:hover, .navigation__hide--btn:focus) ion-icon {
  opacity: 0.7;
}
.navigation__hide--btn ion-icon {
  font-size: 1.4rem;
}
.navigation__tab {
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  font-family: var(--font-ubuntu);
  letter-spacing: 0.75px;
}
.navigation__link {
  font-weight: var(--font-wt-500);
  transition: background 300ms ease;
}
.navigation__link:hover, .navigation__link:focus {
  background: linear-gradient(45deg, currentColor, var(--clr-blue-500)) 0% 100%/100% 3px no-repeat;
}
.navigation__tab--active {
  background: linear-gradient(45deg, currentColor, var(--clr-blue-500)) 0% 100%/100% 3px no-repeat;
}

/*!=========== MEDIA QUERIES ============*/
@media all and (min-width: 720px) {
  html {
    font-size: 100%;
  }

  .navigation__show--btn,
.navigation__hide--btn--wrapper {
    display: none;
  }

  .navigation--main {
    position: relative;
    display: block;
    box-shadow: none;
  }

  .navigation__menu {
    flex-direction: row;
    padding: 0;
  }
}
@media all and (min-width: 1260px) {
  html {
    font-size: 110%;
  }
}
/*!======================================== Homepage  Styling ============================================*/
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@supports (block-size: 100vh) and (padding-inline: 0) {
  .container {
    padding-inline: 2rem;
    block-size: 100vh;
  }
}
@supports not ((block-size: 100vh) and (padding-inline: 0)) {
  .container {
    padding: 0 2rem;
    height: 100vh;
  }
}
.container--home--main {
  height: calc(100vh - 4.5rem);
  justify-content: flex-end;
  background-color: var(--home-main-bg-color);
}
.container--home--main a > *:last-child {
  margin-inline-start: 0.25rem;
}
.container--home--main > .container__text--wrapper {
  animation: left-slide-animation 1500ms ease-out;
}
.container--home--main > .container__image--wrapper {
  animation: right-slide-animation 1700ms ease-out;
}
.container--home--section--one {
  background-color: var(--section-one-bg-color);
  row-gap: 2rem;
}
.container--home--section--one div:first-child {
  order: 1;
}
.container--home--section--two {
  background-color: var(--section-two-bg-color);
  row-gap: 2rem;
}
.container--home--section--two div:first-child {
  order: 1;
}
.container--home--section--one .container__text--wrapper, .container--home--section--two .container__image--wrapper {
  transform: translateX(200px);
  opacity: 0;
  transition: transform 2000ms ease-out, opacity 2000ms ease-out;
}
.container--home--section--one .container__image--wrapper, .container--home--section--two .container__text--wrapper {
  transform: translateX(-200px);
  opacity: 0;
  transition: transform 2000ms ease-out, opacity 2000ms ease-out;
}
.container__text--wrapper {
  align-self: flex-start;
}
.container__text--wrapper h1,
.container__text--wrapper h2,
.container__text--wrapper h5 {
  display: flex;
  flex-direction: column;
}
.container__text--wrapper h1,
.container__text--wrapper h2 {
  color: var(--heading-clr);
  font-family: var(--font-ubuntu);
  font-size: 2rem;
}
.container__text--wrapper h5 {
  color: var(--text-clr);
  margin-block-start: 0.5rem;
  margin-block-end: 1rem;
  font-size: 0.9rem;
  font-weight: var(--font-wt-400);
  line-height: 1.3;
  word-spacing: 1px;
}
.container a span:last-child {
  font-size: 1.3rem;
}
@supports (margin-block-start: 0) {
  .container a {
    margin-block-start: 0.5rem;
  }
}
@supports not (margin-block-start: 0) {
  .container a {
    margin-top: 0.5rem;
  }
}
.container__image--wrapper img {
  display: block;
}
@supports (inline-size: 0) {
  .container__image--wrapper img {
    inline-size: 24rem;
  }
}
@supports not (inline-size: 0) {
  .container__image--wrapper img {
    width: 24rem;
  }
}

/*!================== MEDIA QUERIES ====================*/
@media all and (min-width: 720px) {
  .container {
    flex-direction: row;
    justify-content: space-between;
  }
  .container--home--section--one > div:last-child {
    order: -1;
  }
  .container--home--section--two > div:first-child {
    order: 0;
  }
  .container__text--wrapper {
    align-self: center;
  }
}
@media all and (min-width: 992px) {
  .container {
    justify-content: center;
    gap: 5rem;
  }
}
/*!=========================== Signup Form ============================*/
.form--container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  animation: left-slide-animation 1700ms ease-out;
}
@supports (min-block-size: 0) and (padding-block: 0) {
  .form--container {
    min-block-size: calc(100vh - 4.5rem);
    padding-inline: 0.5rem;
    padding-block: 4rem 2rem;
  }
}
@supports not (min-block-size: 0) {
  .form--container {
    min-height: calc(100vh - 4.5rem);
    padding: 4rem 0.5rem 2rem;
  }
}
.form--container section {
  width: 480px;
  max-width: 95%;
  margin: 0 auto;
  background-color: var(--form-bg);
  padding: 1.5rem;
  border-radius: 10px 40px;
  border: 1px solid var(--clr-blue-100);
  box-shadow: 3px 6px 25px -5px rgba(0, 0, 0, 0.25);
  position: relative;
}
.form--container section h1 {
  display: inline-block;
  position: absolute;
  top: -2.35rem;
  left: 48%;
  transform: translateX(-48%);
  text-align: center;
  background-color: inherit;
  z-index: -1;
  padding: 0.5rem 0.75rem;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  color: var(--heading-clr);
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  font-size: 1.17rem;
  border: 1px solid var(--clr-blue-100);
}
.form__group {
  margin-bottom: 0.4rem;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
@supports (min-inline-size: 100%) and (padding-inline: 0.5rem) {
  .form__group input {
    min-inline-size: 100%;
    padding-inline: 0.5rem;
    padding-block: 0.6rem;
  }
}
@supports not ((min-inline-size: 100%) and (padding-inline: 0.5rem)) {
  .form__group {
    min-width: 100%;
    padding: 0.6rem 0.5rem;
  }
}
.form__group label,
.form__group input {
  letter-spacing: 0.5px;
  color: var(--form-label-clr);
}
.form__group label {
  font-size: 0.85rem;
  line-height: 1.3;
  font-weight: var(--font-wt-500);
}
.form__group input {
  font-size: 0.95rem;
  border-radius: var(--rad-4);
  border: 1px solid var(--clr-blue-100);
  font-family: "Lato", sans-serif;
  background-color: var(--form-input-bg-clr);
}
.form__submit--group input {
  font-family: inherit;
  font-size: 1rem;
  text-transform: uppercase;
  border: none;
  margin-top: 0.5rem;
  letter-spacing: 0.5px;
  padding-block: 0.75rem;
}
.form__link--wrapper p {
  font-size: 0.9rem;
  color: var(--form-label-clr);
  margin-top: 0.5rem;
}
.form__link--wrapper a {
  color: var(--form-link-clr);
  font-weight: var(--font-wt-500);
}
.form__link--wrapper a:hover, .form__link--wrapper a:focus {
  text-decoration: underline 2px currentColor;
}